body {
  background-color: #460808;
  display: grid;
  display: flex;
  color: gold;
  position: relative;
}
@media screen and (min-width: 900px) {
  body {
    background-color: #1b145a;
  }
}
body article {
  display: grid;
  position: absolute;
  transform: translateY(-100px);
  background-color: wheat;
  color: #4c4c69;
  perspective: 50px;
}

.title {
  font-size: 3rem;
}